.big-video {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;

  video {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100vh;
  }
  img {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
  }
  @media screen and (min-width: 800px) {
    top: 80px;
    height: calc(100vh - 80px);
    margin-bottom: 80px;
  }
}
